import React from 'react';
import '../../styles/program.scss';
import ParisWineGallery from './ParisWineGallery';

const ParisWine = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Salon wina w Paryu</h1>
                    {/* <h4>Kto jest zmczony Londynem, jest zmczony yciem: w Londynie jest bowiem wszystko, co ycie moe zaoferowa<span>Samuel Johnson</span></h4> */}
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <ParisWineGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki wedug rozkadu jazdy. Przejazd w kierunku Francji. Zakwaterowanie w hotelu w okolicy Parya, nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie, przejazd do Parya - zwiedzanie miasta z przewodnikiem, m.in.: Grb Napoleona, Pantheon, Luwr i Muzeum Impresjonistw dOrsay. Obiadokolacja. Przejazd na degustacj win. Salon Wina jest wydarzeniem, ktre odbywa si w Paryu w okrelonym czasie i podczas ktrego producenci niemal wszystkich win wystawiaj na salonie (na olbrzymiej powierzchni) swoje trunki z moliwoci ich degustacji. Bdziemy prbowa win pochodzcych z regionw Francji, zarwno te najbardziej popularne, jak i mniej znane. Podczas degustacji istnieje moliwo zakupw.  Pnym wieczorem powrt do hotelu. Nocleg. </p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie, przejazd na dalsze zwiedzanie Parya. Tego dnia udamy si na niezwykle urokliwe Wzgrze Montmartre, na ktrym wznosi si nienobiaa Bazylik Sacre Coeur, zobaczymy Plac Pigalle, Moulin Rouge (z zewntrz), czas wolny na zakupy. Rejs statkiem po Sekwanie. Obiadokolacja. Ponowny przejazd na degustacj win. Powrt do hotelu.  Nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie, wykwaterowanie. Przejazd do Parya. Pary historyczny: Wyspa La Cite z Katedr Notre Dame, Dzielnica aciska z Sorbon, Muzeum Perfum z moliwoci zakupw, Plac Vendome, Opera Garniera, Plac Concorde z Polami Elizejskimi i ukiem Triumfalnym, Wiea Eiffla. Wyjazd do Polski.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>Przyjazd do kraju w godzinach popoudniowych/ nocnych, w zalenoci od miejsca zbirki na trasie powrotnej.</p>
                        </div>
                        <div>
                            <p className="info">*Program jest ramowy i kolejno zwiedzania moe ulec zmianie. Moliwe s przejazdy komunikacj publiczn.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default ParisWine;