import React from 'react';
import '../styles/CityBreak.scss';
import Fade from 'react-reveal/Fade';

import london from '../img/london.jpg';
import barcelona from '../img/cathedral-barcelona.jpg';
import paris from '../img/paris-molin-rouge.jpg';
import prague from '../img/prague.jpg';
import rome from '../img/rome-city.jpg';
import vienna from '../img/vienna.jpg';
import madrid from '../img/madrid.jpg';
import berlin from '../img/berlin.jpg';

import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import Barcelona from './cityBreak/Barcelona';
import Prague from './cityBreak/Prague';
import Rome from './cityBreak/Rome';
import Vienna from './cityBreak/Vienna';
import London from './cityBreak/London';
import Madrid from './cityBreak/Madrid';
import Paris from './cityBreak/Paris';
import Berlin from './cityBreak/Berlin';


const CityBreak = () => {
    return (
        <Router>
            <Route path="/barcelona" component={Barcelona} />
            <Route path="/prague" component={Prague} />
            <Route path="/rome" component={Rome} />
            <Route path="/vienna" component={Vienna} />
            <Route path="/london" component={London} />
            <Route path="/madrid" component={Madrid} />
            <Route path="/paris" component={Paris} />
            <Route path="/berlin" component={Berlin} />

            <section className="city-break" id="city-break">
                <Fade right cascade><h1>City Break</h1></Fade>
                <div>
                    <Fade bottom><div className="london">
                        <div className="cities-photo">
                            <img src={london} alt="" />
                        </div>
                        <h2>Dostojny Londyn</h2>
                        <p>Kto jest zmczony Londynem, jest zmczony yciem: w Londynie jest bowiem wszystko, co ycie moe zaoferowa<span>Samuel Johnson</span></p>
                        <button>
                            <Link to="/london">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade top><div className="barcelona">
                        <div className="cities-photo">
                            <img src={barcelona} alt="" />
                        </div>
                        <h2>Bajeczna Barcelona</h2>
                        {/* <h3>Bdzie cytat</h3> */}
                        <p>To miasto ma czarodziejsk moc (). Zanim si czowiek obejrzy, wejdzie mu pod skr i skradnie dusz.<span>Carlos Ruiz Zafn</span></p>

                        <button>
                            <Link to="/barcelona">czytaj wicej</Link>
                        </button>

                    </div></Fade>

                    <Fade bottom><div className="paris">
                        <div className="cities-photo">
                            <img src={paris} alt="" />
                        </div>
                        <h2>Zakochany Pary</h2>
                        <p>Pary to ruchome wito.<span>Ernest Hemingway</span></p>
                        <button>
                            <Link to="/paris">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade><div className="prague">
                        <div className="cities-photo">
                            <img src={prague} alt="" />
                        </div>
                        <h2>Lunatyczna Praga</h2>
                        <p>Praga nie puszcza nikogo z tych, ktrych pochwycia<span>Angelo Maria Ripellino</span></p>

                        <button>
                            <Link to="/prague">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade><div className="rome">
                        <div className="cities-photo">
                            <img src={rome} alt="" />
                        </div>
                        <h2>Antyczny Rzym</h2>
                        <p>I tak min Nero, jak mija wicher, poar, wojna lub mr, a bazylika Piotra panuje odtd z wyyn watykaskich miastu i wiatu. Wedle za dawnej bramy Kapeskiej wznosi si dzisiaj maleka kapliczka z zatartym nieco napisem Quo vadis, Domine?<span>Henryk Sienkiewicz</span></p>

                        <button>
                            <Link to="/rome">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade><div className="vienna">
                        <div className="cities-photo">
                            <img src={vienna} alt="" />
                        </div>
                        <h2>Muzyczny Wiede</h2>
                        <p>Kto przez radio mwi mi, e dzi w Wiedniu deszcz i mgy.
                        Skd to wzruszenie i dreszcz, Wiede i ty, i deszcz.
                        Naddunajski mokry bruk, krokw twoich znany stuk,
                        blade latarnie na gaz, a w tle zapewne walc.<span>Andrzej Waligrski</span></p>

                        <button>
                            <Link to="/vienna">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade left><div className="madrid">
                        <div className="cities-photo">
                            <img src={madrid} alt="" />
                        </div>
                        <h2>Elegancki Madryt</h2>
                        {/* <h3>jaki tam cytat</h3> */}
                        <p>Z Madrytu do nieba, a w niebie dziurka, by mc go oglda.<span>powiedzenie powstae w czasach krla Karola III</span></p>
                        <button>
                            <Link to="/madrid">czytaj wicej</Link>
                        </button>
                    </div></Fade>

                    <Fade right><div className="berlin">
                        <div className="cities-photo">
                            <img src={berlin} alt="" />
                        </div>
                        <h2>Kosmopolityczny Berlin</h2>
                        <p>Berlin to przede wszystkim zmienno. To miasto cierpi na wieczn niestao, zmienia si nieustannie i to okrela jego tosamo. Nie ma miasta, ktre tyle razy wnosio si na szczyty potgi, by potem upa na samo dno. Stolica adnego kraju nie budzia takiej nienawici, strachu i mioci.<span>Rory Maclean</span></p>

                        <button>
                            <Link to="/berlin">czytaj wicej</Link>
                        </button>
                    </div></Fade>
                </div>

            </section>
        </Router>
    );
}

export default CityBreak;