import React from 'react';
import '../../styles/program.scss';
import FairGallery from './FairGallery';

const Fair = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Jarmarki Boonarodzeniowe</h1>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <FairGallery />
                    <div className="description">
                        <div className="dresden">
                            <h3>Jarmark Striezelmarkt w Drenie</h3>
                            <h4>Program:</h4>
                            <p>Zbirka o umwionej godzinie. Przejazd w kierunku Niemiec. Przyjazd do DREZNA w godzinach porannych. Panoramiczny spacer po miecie podczas ktrego zobaczymy najwaniejsze zabytki Drezna: gmach Galerii Nowych Mistrzw, Akademi Sztuk Piknych, symbol Drezna -koci Najwitszej Marii Panny, Zamek Krlewski, Orszak Ksit, drezdesk katedr, oper Sempera, kompleks muzealny Zwinger (dla chtnych wejcie) w ktrym znajduje si midzy innymi Galeria Starych Mistrzw. Przejcie na Nowy Rynek. Czas wolny na jarmarkach witecznych.
                                <br />
                                <br />
                                <span>Jarmark w Drenie:</span>
                                <br />
                            Najstarszy jarmark boonarodzeniowy w caych Niemczech, drezdeski Jarmark Struclowy Striezelmarkt bdzie obchodzi swoje 584. urodziny. Nazwa "Jarmark Struclowy" powstaa w redniowieczu i wywodzi si od serwowanej wwczas strucli, bez ktrej po dzi dzie nie mog odby si wita Boego Narodzenia w Drenie.
                            Oprcz synnego drezdeskiego strucla, naley sprbowa Glhwein -  popularne grzane wino, ktre jest tu serwowane na kadym kroku oraz ciasta z bakaliami.  Bdziemy podziwia najwiksz witeczn piramid- symbol drezdeskiego jarmarku, oraz chon magi wit wrd licznych straganw oferujcych niezliczone ozdoby choinkowe, drewniane i porcelanowe anioki, biuteria czy bombki na choink, pierniki, lizaki i inne sodycze. Powietrze nasycone jest zapachem grzanego wina, korzennych przypraw i lokalnych, witecznych przysmakw a dookoa przygrywa wspaniaa muzyka.
                            <br />
                            Zbirka w godzinach wieczornych i powrt do kraju. Przyjazd do Polski w godzinach porannych dnia kolejnego.
                            </p>
                        </div>

                        <div className="berlin-market">
                            <h3>Boonarodzeniowe Jarmarki w Berlinie</h3>
                            <h4>Program:</h4>
                            <p>Zbirka i wyjazd w kierunku Berlina. Przyjazd w godzinach porannych. Zwiedzanie stolicy Niemiec spacer z przewodnikiem: zachodnia cz Berlina  synna dzielnica Kudamm i koci Pamici Cesarza Wilhelma. Nastpnie zobaczymy Potsdamerplatz i Sony Center, dzielnic rzdow przy ktrej mieszcz si: Reichstag, Urzd Kanclerski, biura posw i urzdnikw parlamentarnych oraz dodatkowe sale konferencyjne. Zobaczymy Bram Brandenbursk, synn alej Unter den Linden. Nastpnie udajc si w kierunku Alexanderplatz zobaczymy Wysp Muzew, na ktrej znajduje si Muzeum Pergamoskie, katedr Berliner Dom, Nikolaiviertel - dzielnica w. Mikoaja z kocioem pod tym wezwaniem, Marienkirche - koci Mariacki ,wiea TV.
                                <br />
                                <span>Czas wolny na jarmarku.</span>
                                <br />
                                Niemieckie jarmarki s najstarsze, najwiksze i najbardziej popularne wrd odwiedzajcych. W samym Berlinie odbywa si 60 tradycyjnych jarmarkw, na ktrych znajduj si stoiska z rkodzieem, drewnianymi zabawkami i wyrafinowanymi, wymylnymi ozdobami, bombkami i girlandami, biuteri i niezliczon iloci najrniejszych ozdb, ozdbek i witecznych pamitek. Oprcz tego na berliskich straganach mona sprbowa takich specjaw jak: pieczone kasztany, pieczone jabka maczane w czekoladzie bd w lukrze, owoce kandyzowane, praone orzechy i migday, korzenne pierniki, aromatyczne ciasta. Do lokalnych specjaw nale pieczone kartofle i currywurst, czyli grillowana lub pieczona kiebaska wieprzowa posypana przypraw curry. Hitem jarmarkw jest oczywicie Glhwein- grzane wino, ktre mona kupi niemal na kadym kroku. Magiczny witeczny klimat tworzy wspaniaa muzyka, unoszcy si zapach grzanego wina, godzikw, cynamonu, choinek oraz widok masywnych, kilkumetrowych szopek.
                            <br />
                            W godzinach wieczornych zbirka i powrt do Polski.
                            </p>
                        </div>

                        <div className="vienna-market">
                            <h3>Boonarodzeniowe Jarmarki we Wiedniu </h3>
                            <h4>Program:</h4>
                            <p>Zbirka i przejazd w kierunku Wiednia. Przyjazd w godzinach porannych. Zwiedzanie Wiednia: spacer z przewodnikiem podczas ktrego zobaczymy m.in. Katedra, Hofburg, Ratusz, najbardziej reprezentacyjna aleja Ringstrasse, przy ktrym ulokowane s najwaniejsze zabytki. Przejedziemy rwnie w stron synnego Prateru i zobaczymy najstarsze w Europie wesoe miasteczko wraz z Diabelskim Mynem (wszystkie obiekty zwiedzane z zewntrz).
                                <br />
                                Po zakoczeniu zwiedzania czas wolny, ktry moemy spdzi Jarmarkach Boonarodzeniowych.                                <br />
                                Najciekawsze Jarmarki w Wiedniu to m.in Altwiener Christkindlmarkt w ktrym znajdziemy teatrzyk kukiekowy, Jarmarki na Rathausplatz, Karlsplatz i na dziedzicu paacu Schonbrunn. Miejsca te przed witami Boego Narodzenia przeistaczaj si w krain migoczcych wiate, drewnianych chatek, zimowych zabaw, karuzeli, wspaniaej muzyki. Powietrze nasycone jest zapachem grzanego wina, korzennych przypraw i lokalnych, witecznych przysmakw: jabka w karmelu, rne owoce w czekoladzie, apfelstrudel a take sery i wdliny. Kupi mona lokalne rkodzieo: haftowane obrusy, dziergane narzuty, czapki, szaliki ozdoby choinkowe tj. bombki, anioki itd.
                            </p>
                        </div>

                        <div className="prague-market">
                            <h3>Boonarodzeniowe Jarmarki w Pradze</h3>
                            <h4>Program:</h4>
                            <p>Zbirka i wyjazd w kierunku Pragi. Przyjazd w godzinach porannych. Zwiedzanie Pragi: spacer z przewodnikiem podczas ktrego zobaczymy: klasztor na Strahovie, Hradczany z Zamkiem Krlewskim i katedr w. Wita, w. Wacawa i w. Wojciecha, synn Loret (kompleks barokowych budynkw sakralnych); Mala Strana - romantyczna dzielnica pooona na lewym brzegu Wetawy, u stp praskiego zamku. Obfituje w pikne kamienice, uliczki i kocioy. Nastpnie zobaczymy Most Karola. Nastpnie najbardziej reprezentacyjny plac miasta- Rynek Staromiejski na ktrym zlokalizowane s: ratusz, synny zegar Orloj, pomnik Jana Husa, Uniwersytet Karola z XIV wieku. Zobaczymy rwnie Plac Wacawa.
                                <br />
                            Czas wolny na Rynku Staromiejskim, gdzie znajduje si centralna cz jarmarkw. Liczne kolorowe stragany oferuj: charakterystyczne czeskie ozdoby choinkowe, rkodzieo, zimowa odzie (czsto rcznie wykonana), szklane wiedzideka, biuteria, lokalne potrawy i przysmaki: rdelnik, medovina (mid pitny), wino z miodem, ziemniaki pod rnymi postaciami: z boczkiem i kapust, smaone zakrcane spiralki, chipsy, pieczona szynka. Powrt do autokaru w godzinach wieczornych. Nocny przejazd do Tarnowa. Przyjazd w nocy/ nad ranem dnia.
                            </p>
                        </div>

                        <div className="budapest-market">
                            <h3>Boonarodzeniowe Jarmarki w Budapeszcie</h3>
                            <h4>Program:</h4>
                            <p>Wyjazd z miejsca zbirki. Przejazd w kierunku Budapesztu. Przyjazd w godzinach porannych. Zwiedzanie stolicy Wgier: spacer z przewodnikiem podczas ktrego zobaczymy: zwiedzanie miasta: Wzgrze Zamkowe z Baszt Ryback, gotycki koci w. Macieja, Zamek Krlewski (z zewntrz), Najstarszy most w Budapeszcie- Most  acuchowy oraz inne ciekawe konstrukcje tego typu: Most Magorzaty Most Elbiety, Most Wolnoci, gmach Opery, Bazylika w. Stefana, gmach Parlamentu.
                            <br />
                            Czas wolny na jarmarku. Na budapesztaskich jarmarkach mona kupi unikatowe wyroby sztuki ludowej i rzemiosa artystycznego. Wszystkie wykonane rcznie, z naturalnych materiaw doskonale nadaj si na witeczne upominki. Magiczn atmosfer tworz unoszce si nad placem zapachy piernikw, cynamonu, grzanego wina oraz choinek. Mona bdzie sprbowa lokalnych przysmakw: Lngos, Krtskalcs, Krampampuli i inne grzane wina, kiebasy i wyroby misne, langosze, czyli smaone w gbokim tuszczu paskie placki z mki pszennej, ziemniakw i mleka. Na jarmarku boonarodzeniowym w Budapeszcie popularna jest rwnie kapusta. Na jarmarku nie moe zabrakn sodyczy. Najpopularniejsze to: marcepan oraz czekolada, kurtoszkoacze (krtskalcs), czyli sodkie drodowe ciasto nawijane na walce i pieczone nad ogniem.
                            <br />
                            W godzinach wieczornych zbirka i wyjazd w kierunku Polski.
                            </p>
                        </div>


                    </div>
                </div>
            </section>
        </>
    );
}

export default Fair;