import React from 'react';
import '../styles/program.scss';
import ItalyGallery from './ItalyGallery';

const Italy = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Wochy</h1>
                    <h2>Soneczna Italia</h2>
                    <h4>Wochy to kraj peen licznych pokus pocigajcych do zboczenia z drogi prostej i uczciwej.
                       <span>Zygmunt III Waza</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <ItalyGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki, przejazd w kierunku Woch. Przyjazd w okolice Bergamo/Mediolanu nocleg. </p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Wykwaterowanie. Zwiedzanie Mediolanu z przewodnikiem: Plac Katedralny oraz synn katedr  Duomo, bdc jednym z najwikszych i najpikniejszych kociow na wiecie, galerie Vittorio Emanuele II, gmach najsynniejszej na wiecie opery la Scala. Zobaczymy rwnie Zamek Sforzw, wielki miejski - Park Sempione oraz uk triumfalny Arco della Pace. Czas wolny. Przejazd w kierunku Toskanii. Zakwaterowanie w hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Przejazd na zwiedzanie Florencji. Odwiedzimy katedr Santa Maria del Fiore, zobaczymy Baptysterium z drzwiami wykonanymi z brzu. Spacer na Piazza della Signora przy ktrym znajduje si Palazzo Vecchio. Zobaczymy gmach Galerii Uffizi oraz przejdziemy na synny Ponte Vecchio. Czas wolny. Przejazd do LUKKI, gdzie spacerujc nastrojowymi uliczkami zobaczymy katedr di San Martino oraz koci San Michele in Foro. Powrt do hotelu. Obiadokolacja. Nocleg. </p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Przejazd do Pizy. Przejcie na  Plac Cudw, na ktrym zobaczymy jedne z najsynniejszych zabytkw Woch: Krzyw Wie, katedr Santa Maria Assunta i Baptysterium. Nastpnie przejazd do Sieny. Spacer na Piazza del Campo bdcy wizytwk Sieny. Znajduj si przy nim takie zabytki jak: Palazzo Pubblico wraz z dzwonnic Torre del Mangia. Po przeciwlegej stronie znajduje si fontanna Fonte Gaia. Nawiedzimy katedr Santa Maria Assunta oraz bazylik San Domenico, w ktrej znajduj si relikwie w. Katarzyny bdcej patronk Sieny. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Rzymu. Zwiedzanie Bazyliki w. Piotra w Watykanie. Nastpnie spacer po Rzymie barokowym, gdzie zobaczymy przepikne zabytki, place, fontanny: Piazza Navona, Panteon, Fontanna di Trevi, Plac Hiszpaski wraz ze Schodami Hiszpaskimi. Czas wolny na kaw, lody i kawaek pizzy. Nastpnie przejazd na zwiedzanie bazyliki Santa Maria Maggiore. Powrt do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>niadanie, wykwaterowanie. Przejazd na dalsze zwiedzanie Rzymu: Lateran, gdzie zobaczymy bazylik w. Jana oraz wite Schody. Spacer w kierunku Rzymu antycznego. Zobaczymy Koloseum, uk Tytusa, przejdziemy synn via dei Fori Imperiali skd podziwia bdziemy fora cesarskie, wejdziemy na Kapitol, dojdziemy do Placu Weneckiego, gdzie znajduje si olbrzymi Pomnik Ojczyzny. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Neapolu. Zwiedzanie miasta z przewodnikiem. Zobaczymy katedr w. Januarego, spacer nastrojowymi uliczkami Neapolu, gdzie obejrzymy synne szopki neapolitaskie. Zobaczymy rwnie Castel dellOvo, zwany te Zamkiem Jajecznym, zbudowany w VI w. na niewielkiej wysepce, Castel Nuovo, Palazzo Reale di Napoli- dawn rezydencj wadcw hiszpaskich, Plac Plebiscytu, na ktrym w 1860 roku odbyo si gosowanie dotyczce poczenia poszczeglnych krlestw we Woszech, co zakoczyo si utworzeniem Zjednoczonego Krlestwa Woch. Przeprawa promowa na Capri- jedn z najbardziej elitarnych wysp Italii, bdc klejnotem Zatoki Neapolitaskiej. Capri szczyci si bajecznym krajobrazem tj. rdziemnomorska szata rolinna, strome klify, biae kamienice, urokliwe uliczki, ttnice yciem porty i turkusowe wody zatoki, ponadto niezrwnana kuchnia (to wanie z Capri pochodzi rozsawiona na caym wiecie saatka caprese). Po dniu penym wrae przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="eighth-day">
                            <h5>Dzie VIII</h5>
                            <p>niadanie, wykwaterowanie, przejazd do Asyu. Spacer po malowniczym redniowiecznym miasteczku, bazylika w. Franciszka, Plac Ratuszowy- dawne forum, na ktrym znajdowaa si witynia Minerwy, po ktrej do dzi zachowaa si fasada. Czas wolny. Przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="ninth-day">
                            <h5>Dzie IX</h5>
                            <p>Po niadaniu przejazd do San Marino - najstarszej republiki wiata.  Jest to jedno z najmniejszych pastw wiata, pooone wysoko na wzgrzu Monte Titano i synie z przepiknej starwki i ogromnego zamku. Czas na zakupy, podczas ktrych bdzie mona skorzysta z dobrych okazji, bowiem San Marino synie z okazjonalnych cen. Nastpnie przejazd do Ravenny. Najwaniejszym zabytkiem miasta jest Bazylika San Vitale z bezcennymi mozaikami pochodzcymi z V i VI wieku. Spacer po miecie. Nastpnie przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="tenth-day">
                            <h5>Dzie X</h5>
                            <p>niadanie, wykwaterowanie. Przejazd na zwiedzanie Werony. Spacer z przewodnikiem. Zobaczymy m.in.: Piazza Bra ze synnym amfiteatrem, Piazza dErbe- przepikny plac, na ktrym znajduj si stoiska z owocami i warzywami oraz pomnik Dantego, zobaczymy rwnie synny balkon Julii. Nastpnie przejazd do Wenecji. Przeprawa odzi na Plac w. Marka, gdzie zobaczymy Bazylik w. Marka, dzwonnic, Paac Dow, Spacer uroczymi uliczkami, ktrymi dojdziemy do Mostu Rialto spinajcego brzegi Canale Grande. Czas wolny. Powrt do autokaru, przejazd do hotelu. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="eleventh-day">
                            <h5>Dzie XI</h5>
                            <p>niadanie, wykwaterowanie, wyjazd w kierunku Polski. Przyjazd dnia kolejnego w godzinach uzalenionych od miejsca zbirki.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Italy;