import React from 'react';
import '../styles/program.scss';
import FranceGallery from './FranceGallery';

const France = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Francja</h1>
                    <h2>Francuskie Impresje</h2>
                    <h4>Kady czowiek ma dwie ojczyzny: swoj wasn oraz Francj.e de Vegi.
                       <span>Henri de Bornier</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <FranceGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki i przejazd w kierunku Niemiec i Francji. Przyjazd do Strasburga. Zakwaterowanie i nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Zwiedzanie Strasburga ktrego, zabytkowe centrum zostao w caoci wpisane na List wiatowego Dziedzictwa UNESCO. Strasburg to rwnie miasto w ktrym przeszo spotyka si z nowoczesnoci, polityka z histori a alzacka mentalno z europejskim kosmopolityzmem. Spacer po Grand Ile, ktrej gwnym zabytkiem jest katedra Notre-Dame, nastpnie do urokliwej Petite France- dzielnicy Strasburga, w ktrej znajduj si hotele i knajpki. Przejazd do Nancy  miasta bdcego historyczn stolic Lotaryngii. Najwaniejszym miejscem jest place Stanislas, z pomnikiem Stanisawa Leszczyskiego, ktry zamieni Nancy w klejnot XVIII-wiecznej urbanistyki. Zobaczymy rwnie uk Triumfalny oraz  katedr Notre-Dame de Bon Secours, w ktrej znajduje si nagrobek krla. Nastpnie przejazd do Metz. To rzymskie miasto jest obecnie stolic Lotaryngii. Spacer wrd kanaw. Obiadokolacja. Przejazd do hotelu. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>Po niadaniu wyjazd na zwiedzanie Szampanii. Przyjazd do Reims, w ktrym zwiedzimy gotyck Katedr Notre-Dame, ktra uchodzi za najbardziej harmonijn we Francji, zwan rwnie Katedr Aniow. witynia bya miejscem koronacji krlw Francji. Nastpnie przejazd do pernay, bdcego gwnym orodkiem produkcji szampana. Udamy si do jednej z piwnic na degustacj. Nastpnie przejazd w kierunku Parya. Spacer po malowniczym wzgrzu Montmartre, gdzie zobaczymy nienobia bazylik Sacr-Cur, Moulin Rouge, Plac Pigalle. Przejazd na obiadokolacj a nastpnie na nocleg.</p>
                        </div>

                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Przejazd na dalsze zwiedzanie Parya. Zwiedzanie Luwru- najwikszego muzeum na wiecie, ktre prezentuje sztuk od czasw staroytnych po XIX wiek, zobaczymy synn Mona Lis, Wenus z Milo, Kodeks Hamurabiego i inne cenne dziea sztuki. Nastpnie zwiedzanie miasta: Katedra Notre-Dame (z takiej odlegoci z jakiej, to bdzie moliwe), spacer po Dzielnicy aciskiej, gdzie zobaczymy Panteon, Sorbon, dawne termy rzymskie, koci w. Seweryna. Czas wolny na zakupy, lunch lub kaw. Przejazd pod wie Eiffla, wejcie lub wjazd wind. Dla chtnych rejs po Sekwanie. Przejazd na obiadokolacj. Nocleg.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>niadanie i wyjazd do Wersalu. Zwiedzenie paacu krlewskiego, bdcego symbolem francuskiej monarchii absolutnej. Zwiedzanie Galerii Zwierciadlanej, Kaplicy Krlewskiej, Salonu Wenus, Sypialni Krlowej. Nastpnie powrt do Parya, gdzie zobaczymy jeszcze: Plac Concorde, Pola Elizejskie, uk Triumfalny, przepikny gmach opery Garniera, Plan Vendome, przy ktrym mieszka Fryderyk Chopin, koci Marii Magdaleny. Obiadokolacja. Powrt do hotelu. Nocleg.</p>
                        </div>

                        <div className="sixth-day">
                            <h5>Dzie VI</h5>
                            <p>niadanie. Przejazd Mont Sant Michel. Zwiedzanie klasztoru pooonego na granicy Normandii i Bretanii na wyspie zwanej Mont-Tombe (Grobowcem na Wzgrzu). Ze skromnej VIII-wiecznej kaplicy wyroso ogromne benedyktyskie opactwo wzbudzajce respekt ze wzgldu na rozmiary oraz dziaanie natury. Architektura sanktuarium oraz zatoka z duymi pywami morskimi sprawiaj, i jest to jedno z najchtniej odwiedzanych miejsc we Francji.  Po zwiedzaniu przejazd na obiadokolacj i nocleg.</p>
                        </div>

                        <div className="seventh-day">
                            <h5>Dzie VII</h5>
                            <p>niadanie. Przejazd do La Rochelle, ktrego dum jest stary port otoczony okazaymi budowlami i najwikszym portem jachtowym na wybrzeu atlantyckim Francji, nad ktrym gruj dwie wiee. Spacer po miecie, nastpnie przejazd do miejscowoci Cognac, gdzie odwiedzimy jedn z destylarni, gdzie dowiemy si jak powstaje trunek. Degustacja koniaku. Przejazd na obiadokolacj, nastpnie do hotelu na nocleg.</p>
                        </div>

                        <div className="eighth-day">
                            <h5>Dzie VIII</h5>
                            <p>Po niadaniu wyjazd do Bordeaux, wzniesionego w zakolu rzeki Garonny. Spacer po reprezentacyjnym XVIII-wiecznym centrum oraz wzdu nadbrzea, gdzie wznosz si klasycystyczne fasady. Zobaczymy gmach Grand Thtre, reprezentacyjny Place de la Bourse, Esplanade des Quinconces z fontannami i pomnikami, z ktrych najwaniejszy jest Monument aux Girondins, upamitniajcy yrondystw posanych na gilotyn w czasie Rewolucji Francuskiej. Przejazd w okolice Tuluzy. Obiadokolacja. Nocleg.</p>
                        </div>

                        <div className="ninth-day">
                            <h5>Dzie IX</h5>
                            <p>Po niadaniu przejazd na zwiedzanie redniowiecznego miasta obronnego Carcasonne, ktre wpisane jest na list UNESCO. Zobaczymy mury obronne, Bazylik St-Nazaire i Chteau-zamek, bdcy twierdz w twierdzy. Przejazd do Awinionu. Spacer po prowansalskim miasteczku, podczas ktrego zobaczymy mury obronne miasta, Paac Papieski, Katedr Notre-Dame-des-Domes i synny most awinioski  Most w. Benezeta. Przejazd na kolacj, nastpnie do hotelu na nocleg.</p>
                        </div>

                        <div className="tenth-day">
                            <h5>Dzie X</h5>
                            <p>Przejazd do Cannes. Spacer Alej Gwiazd, ktr dojdziemy do Paacu Festiwalowego, gdzie co roku w maju odbywa si gala, podczas ktrej przyznawane s Zote Palmy. Czas wolny na play. Dla chtnych rejs na jedn z wysp w. Magorzaty, gdzie wiziony by tajemniczy czowiek w elaznej masce, lub na wysp  w. Honorata, bdcej urokliwym zacisznym miejscem, gdzie unosi si intensywny zapach pinii oraz syncej z znakomitego wina produkowanego przez Cystersw zamieszkujcych wysp. Obiadokolacja. Przejazd do hotelu. Nocleg.</p>
                        </div>
                        <div className="eleventh-day">
                            <h5>Dzie XI</h5>
                            <p>Po niadaniu przejazd przez kanion rzeki Verdon, gdzie podziwia bdziemy przepikne, malownicze pooone wioski oraz toczc swoje wody rzek. Przejazd do Grasse, bdcego  orodkiem kwiatowego przemysu perfumeryjnego. Zwiedzanie Muzeum Perfum, moliwo zakupw. Obiadokolacja. Nocleg.</p>
                        </div>
                        <div className="twelfth-day">
                            <h5>Dzie XII</h5>
                            <p>niadanie. Wykwaterowanie. Przejazd do Nicei: Promenada Anglikw, barokowa katedra  Ste-Rparate, wskie uliczki Starego Miasta, na ktrym znajduje si najwikszy targ w regionie. Przejazd do Ksistwa Monako. Zwiedzanie Starego Miasta Monako: pikny gmach oceanarium pooony na skarpie nad morzem, Katedra w. Mikoaja, spacer do Paacu Ksicego (z zewntrz). Zobaczymy czarujc panoram na Stary Port. Przejazd do Monte Carlo, podejcie pod synne kasyno. Po zwiedzaniu przejazd w kierunku Woch na obiadokolacj i nocleg. </p>
                        </div>

                        <div className="thirteenth-day">
                            <h5>Dzie XIII</h5>
                            <p>niadanie, przejazd w kierunku Polski. Godziny przyjazdu uzalenione s od miejsca zbirki.</p>
                        </div>

                    </div>
                </div>
            </section>

        </>
    );
}

export default France;