import React from 'react';
import '../../styles/program.scss';
import BerlinBlackFridayGallery from './BerlinBlackFridayGallery';

const BerlinBlackFriday = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Black Friday w Berlinie</h1>
                    {/* <h4>Kto jest zmczony Londynem, jest zmczony yciem: w Londynie jest bowiem wszystko, co ycie moe zaoferowa<span>Samuel Johnson</span></h4> */}
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <BerlinBlackFridayGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>WZbirka i wyjazd w kierunku Berlina. Przyjazd w godzinach porannych. Zwiedzanie stolicy Niemiec spacer z przewodnikiem: zachodnia cz Berlina  synna dzielnica Kudamm i koci Pamici Cesarza Wilhelma. Nastpnie zobaczymy Potsdamerplatz i Sony Center, dzielnic rzdow przy ktrej mieszcz si: Reichstag, Urzd Kanclerski, biura posw i urzdnikw parlamentarnych oraz dodatkowe sale konferencyjne. Zobaczymy Bram Brandenbursk, synn alej Unter den Linden. Nastpnie udajc si w kierunku Alexanderplatz zobaczymy Wysp Muzew, na ktrej znajduje si Muzeum Pergamoskie, katedr Berliner Dom, Nikolaiviertel - dzielnica w. Mikoaja z kocioem pod tym wezwaniem, Marienkirche - koci Mariacki ,wiea TV.
                            <br />
                            Czas wolny na Alexanderplatz, gdzie znajduj si centra handlowe,  liczne sklepy, lokale gastronomiczne. Po zakupach zbirka i powrt do Polski.
                            </p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default BerlinBlackFriday;