import React from 'react';
import '../../styles/program.scss';
import BerlinGallery from './BerlinGallery';

const Berlin = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Kosmopolityczny Berlin</h1>
                    <h4>Berlin to przede wszystkim zmienno. To miasto cierpi na wieczn niestao, zmienia si nieustannie i to okrela jego tosamo. Nie ma miasta, ktre tyle razy wnosio si na szczyty potgi, by potem upa na samo dno. Stolica adnego kraju nie budzia takiej nienawici, strachu i mioci.<span>Rory Maclean</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <BerlinGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka i wyjazd w kierunku Berlina. Przyjazd w godzinach porannych. Zwiedzanie stolicy Niemiec spacer z przewodnikiem: zachodnia cz Berlina  synna dzielnica Kudamm i koci Pamici Cesarza Wilhelma. Nastpnie zobaczymy Potsdamerplatz i Sony Center, dzielnic rzdow przy ktrej mieszcz si: Reichstag, Urzd Kanclerski, biura posw i urzdnikw parlamentarnych oraz dodatkowe sale konferencyjne. Zobaczymy Bram Brandenbursk, synn alej Unter den Linden, synne przejcie graniczne Checkpoint Charlie. Nastpnie udajc si w kierunku Alexanderplatz zobaczymy Wysp Muzew, na ktrej znajduj si:  Muzeum Pergamoskie, Muzeum Bodego, Nowe Muzeum, Stare Muzeum oraz Stara Galeria Narodowa. Czas wolny na zwiedzanie wybranego muzeum (wczeniej naley zgosi  ktrego, w celu rezerwacji). Zobaczymy rwnie katedr Berliner Dom, Nikolaiviertel - dzielnica w. Mikoaja z kocioem pod tym wezwaniem, Marienkirche - koci Mariacki ,synna wie TV. Czas wolny. Zbirka w godzinach wieczornych i przejazd w kierunku Polski. Przyjazd w godzinach nocnych lub porannych dnia kolejnego (w zalenoci od miejsca zbirki).</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Berlin;