import React, { Component } from 'react';
import '../styles/Offer.scss';
import Pulse from 'react-reveal/Pulse';
import Fade from 'react-reveal/Fade';

import trip from '../img/trip.jpg';
import camp from '../img/camp.jpg';
import pilgrimage from '../img/pielgrzymka.jpg';
class Offer extends Component {
    state = {
        showTripText: false,
        showPilgrimagesText: false,
        showCampText: false,
        showCityText: false,
        showthemedTripText: false,
    }

    handleClickTrip = () => {
        this.setState({
            showTripText: !this.state.showTripText,
        })
    }
    handleClickPilgrimage = () => {
        this.setState({
            showPilgrimagesText: !this.state.showPilgrimagesText,
        })
    }
    handleClickCamp = () => {
        this.setState({
            showCampText: !this.state.showCampText,
        })
    }

    render() {
        return (
            <>
                <section className="offer" id="offer">
                    <Pulse><h1>Nasz oferta</h1></Pulse>

                    <Fade bottom cascade> <div>

                        <div className="trip">
                            <p className={this.state.showTripText ? "show" : null}>W naszej ofercie prezentujemy Pastwu szerok gam wycieczek zaczynajc od krtkich lotniczych i autokarowych City Breakw do europejskich metropolii, poprzez dugie wycieczki objazdowe, na ktrych trasie znajduje si wiele piknych miejsc i atrakcji, po wyjazdy tematyczne. Nasze oferty skierowane s do klienta grupowego i s to proponowane warianty. Jednak prawie wszystkie oferty s elastyczne i podlegaj modyfikacjom na yczenie Klienta. Nasze programy s szyte na miar i z wielk chci stworzymy dla Pastwa wymarzony program na wycieczk.</p>
                            <img src={trip} alt="" />
                            <h5>Wycieczki</h5>
                            <button onClick={this.handleClickTrip}>{this.state.showTripText ? 'UKRYJ' : 'DOWIEDZ SI WICEJ'}</button>
                        </div>
                        <div className="pilgrimages">
                            <p className={this.state.showPilgrimagesText ? "show" : null}>W Europie znajduje si niezliczona ilo przepiknych kociow, kaplic, kapliczek, sanktuariw, bdcych pamitk po niezwykych wydarzeniach takich jak objawienia Maryjne, objawienia aniow, dziaalno witych. W naszej ofercie nie moe zabrakn miejsc kultu religijnego. Naszym celem jest spotkanie z naszymi patronami i ordownikami, modlitwa na ich grobach, proba o ich wstawiennictwo. Chcemy wraca do wydarze, ktre miay miejsce, poczu je w naszych sercach, dotkn tych miejsc i oczywicie podziwia dziea architektury stworzone dla pokole.</p>
                            <img src={pilgrimage} alt="" />
                            <h5>Pielgrzymki</h5>
                            <button onClick={this.handleClickPilgrimage}>{this.state.showPilgrimagesText ? 'UKRYJ' : 'DOWIEDZ SI WICEJ'}</button></div>
                        <div className="camp">
                            <p className={this.state.showCampText ? "show" : null}>Wszyscy doskonale znamy powiedzenie, e podre ksztac. Wycieczki s wietnym uzupenieniem szkolnej edukacji (a moe odwrotnie?), dlatego przygotowalimy rwnie ofert dla naszych najmodszych Klientw. Proponujemy rne wycieczki z elementami edukacyjno-poznawczymi. Jednak doskonale zdajemy sobie spraw, e odpoczynek naley si kademu, dlatego mamy przygotowane rwnie oferty na obozy modzieowe, na ktrych uczestnicy oprcz zwiedzania maj duo czasu na zabaw i odpoczynek. Programy s elastyczne, podlegaj modyfikacjom na yczenie Klienta. Oferty wietnie nadaj si rwnie dla dorosych.</p>
                            <img src={camp} alt="" />
                            <h5>Obozy modzieowe</h5>
                            <button onClick={this.handleClickCamp}>{this.state.showCampText ? 'UKRYJ' : 'DOWIEDZ SI WICEJ'}</button></div>

                    </div></Fade>

                </section>
            </>
        );
    }
}

export default Offer;