import React from 'react';
import '../../styles/program.scss';
import LondonGallery from './LondonGallery';

const Vienna = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Dostojny Londyn</h1>
                    <h4>Kto jest zmczony Londynem, jest zmczony yciem: w Londynie jest bowiem wszystko, co ycie moe zaoferowa<span>Samuel Johnson</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <LondonGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka na lotnisku. Przelot do Londynu. Przejazd na zwiedzanie miasta. Spacer przy Parliament Square ze synnym Big Benem, Whitehall, Trafalgar Square. Czas wolny na zwiedzanie National Gallery, spacer najsynniejsz ulic handlow Londynu  Oxford Street. Obiadokolacja. Przejazd do hotelu. Nocleg</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie. Przejazd do centrum miasta. Rejs po Tamizie, podczas ktrego zobaczymy wiele zabytkw miasta, m.in. potn fortec Tower of London, najsynniejszy most Wielkiej Brytanii Tower Bridge, Monument- pomnik upamitniajcy wybuch wielkiego poaru w 1666 roku. Zwiedzanie Westminster Abbey  miejsca koronacji i pogrzebw krlw Anglii, Spacer do Buckingham Palace, bdcego oficjaln rezydencj krlowej. Przejazd do Muzeum Figur Woskowych Madame Tussauds. Wizyta na West Endzie, gdzie znajduje si niezliczona liczba pubw, restauracji, sklepw i teatrw. Obiadokolacja. Czas wolny. Dla chtnych wejcie do jednego z londyskich pubw, ktra stanowi doskona okazj do sprbowania tradycyjnego angielskiego piwa oraz przysmakw. Powrt do hotelu pnym wieczorem. Nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>Po niadaniu wizyta w Katedrze w. Pawa, bdcej symbolem odrodzenia Londynu po wielkim poarze, nastpnie przejazd na zwiedzanie British Museum-  jednego z najwikszych muzew staroytnych na wiecie. Przejcie do Covent Garden, czas wolny, obiadokolacja w restauracji. Powrt do hotelu. Nocleg. </p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Wykwaterowanie. Transfer na lotnisko, wylot do Polski.</p>
                        </div>
                        <div>
                            <p className="info">*Program jest ramowy i kolejno zwiedzania moe ulec zmianie. Moliwe s przejazdy komunikacj publiczn.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Vienna;