import React from 'react';
import '../../styles/program.scss';
import MadridGallery from './MadridGallery';

const Madrid = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Elegancki Madryt</h1>
                    <h4>Z Madrytu do nieba, a w niebie dziurka, by mc go oglda.<span>powiedzenie powstae w czasach krla Karola III</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <MadridGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Zbirka na lotnisku. Przelot do Madrytu. Transfer do hotelu. Spacer gwn arteri miasta- Gran Via, podczas ktrego zobaczymy najbardziej okazae zabytki oraz nowoczesne siedziby wiatowych korporacji. Powrt do hotelu, nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>Po niadaniu udamy si na zwiedzanie. Tego dnia zobaczymy: Plac Hiszpaski z pomnikiem Cervantesa, zobaczymy dwa wane madryckie place: Puerta del Sol, bdcy rodkiem Hiszpanii i niezwykle uroczy Plaza Mayor. Nastpnie spacer po najstarszej czci miasta Madrycie Habsburgw. Przerwa na tapas w Mercado San Miguel -zabytkowej hali targowej, gdzie skosztowa mona przeksek z rnych regionw Hiszpanii. Nastpnie dalsze zwiedzanie: koci w. Izydora, bazylika w. Franciszka, Paac Krlewski  symbol hiszpaskiej monarchii wraz ze wspaniaymi XIX-wiecznymi ogrodami, katedra Almudena, Powrt do hotelu. Obiadokolacja i nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Zwiedzanie muzeum Prado, ktre kryje wspania kolekcj dzie Velzqueza, Goi i innych wybitnych artystw. Zobaczymy rwnie dworzec kolejowy Atocha o jego unikatowym charakterze wiadczy architektura oraz znajdujcy si w holu tropikalny ogrd.  Przejazd na stadion Santiago Bernabu. Czas wolny na zakupy. Powrt do hotelu, nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie. Wykwaterowanie. Transfer na lotnisko. Przelot do Polski.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Madrid;