import React from 'react';
import '../../styles/program.scss';
import ParisGallery from './ParisGallery';

const Paris = () => {
    return (
        <>
            <section className="trip-program">
                <div className="title">
                    <h1>Dostojny Londyn</h1>
                    <h4>Pary to ruchome wito.<span>Ernest Hemingway</span></h4>
                    <div className="hide" onClick={() => window.history.back()}><span>X</span></div>
                </div>
                <div className="content">
                    <ParisGallery />
                    <h4>Program:</h4>
                    <div className="description">
                        <div className="first-day">
                            <h5>Dzie I</h5>
                            <p>Wyjazd z miejsca zbirki wedug rozkadu jazdy. Przejazd w kierunku Francji. Zakwaterowanie w hotelu w okolicy Parya, nocleg.</p>
                        </div>
                        <div className="second-day">
                            <h5>Dzie II</h5>
                            <p>niadanie, przejazd do Parya - zwiedzanie miasta z przewodnikiem, m.in.: Kompleks Inwalidw i Grb Napoleona, najwiksze muzeum wiata Luwr i Muzeum Impresjonistw dOrsay,  rejs statkiem po Sekwanie, podczas ktrego zobaczymy najwiksze atrakcje Parya. Obiadokolacja. Powrt do hotelu, nocleg.</p>
                        </div>
                        <div className="third-day">
                            <h5>Dzie III</h5>
                            <p>niadanie. Tego dnia udamy si do Wersalu- majestatycznego paacu krlewskiego, bdcego symbolem francuskiej monarchii absolutnej. Nastpnie przejazd do Parya na dalsze zwiedzenie: Wzgrze Montmartre z Bazylik Sacre Coeur, Plac Pigalle, Moulin Rouge (z zewntrz), czas wolny na zakupy. Nastpnie przejazd na Pola Elizejskie i wejcie na uk Triumfalny.Obiadokolacja. Powrt do hotelu, nocleg.</p>
                        </div>
                        <div className="fourth-day">
                            <h5>Dzie IV</h5>
                            <p>niadanie, wykwaterowanie. Przejazd do Parya. Tego dnia bdziemy zwiedza: Wysp Cite z Katedr Notre Dame, spacer po urokliwej Dzielnicy aciskiej z Sorbon i Pantheonem. Zobaczymy przepikny renesansowy Ratusz, przejazd do Muzeum Perfum z moliwoci zakupw, spacer na Plac Vendome,  zobaczymy rwnie przepikny gmach Opery Garniera i najwikszy paryski plac- Plac Concorde. Na koniec wejcie bd wjazd na Wie Eiffla. Po caodziennym zwiedzaniu wyjazd do Polski.</p>
                        </div>
                        <div className="fifth-day">
                            <h5>Dzie V</h5>
                            <p>Przyjazd do kraju w godzinach popoudniowych/ nocnych, w zalenoci od miejsca zbirki na trasie powrotnej. </p>
                        </div>
                        <div>
                            <p className="info">*Program jest ramowy i kolejno zwiedzania moe ulec zmianie. Moliwe s przejazdy komunikacj publiczn.</p>
                        </div>

                    </div>
                </div>
            </section>
        </>
    );
}

export default Paris;